:root {
  --red: #9a0000;
  --green: #2d9b63;
  --blue: #006599;
  --p: #000000;
  --b: #ffffff;
}
html {
  overflow-y: scroll;
}
body {
  background-color: #fffffe;
}
input {
  border: none;
}

.card-profile:hover {
  filter: brightness(100%) !important;
}

.searchBar {
  border-bottom: 1px solid var(--p);
  border-top: 0px solid #aaaaaa;
  border-right: 0px solid #aaaaaa;
  border-left: 0px solid #aaaaaa;
  width: 500px;
  background: #ffffff;
}

.statsContent {
  padding-top: 6%;
}
.HomePageContent {
  padding-top: 8%;
  padding-left: 1%;
}

.aboutPageContent {
  padding-top: 3%;
  padding-left: 4%;
}

.card-body {
  flex: 1 1 auto;
  min-height: 1px;
  padding: 0.5rem;
}

.profiles {
  padding-left: 5%;
}
.searchBar:focus {
  border-bottom: 1px solid #aaaaaa;
  border-top: 0px solid #aaaaaa;
  border-right: 0px solid #aaaaaa;
  border-left: 0px solid #aaaaaa;
  width: 500px;
  background: #ffffff;
}

.searchBar:hover {
  border-bottom: 1px solid #aaaaaa;
  border-top: 0px solid #aaaaaa;
  border-right: 0px solid #aaaaaa;
  border-left: 0px solid #aaaaaa;
  width: 500px;
  background: #ffffff;
}

.navbar {
  background-color: transparent !important;
}

.container-fluid {
  padding-left: 5% !important;
  height: 100%;
  padding-top: 5% !important;
}
.homeContent {
  padding-top: 2% !important;
}

.btn {
  border-radius: 60px !important;
  width: 82%;
}

.card-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  height: 100%;
  width: 100%;
}

.card {
  display: inline-block;
  background-color: white;
  border-radius: 5px;
  border: 1px solid #d6d6d6;
  margin: 1vmin;
  padding: 0 10px;
  width: 30%;
}

.card:hover {
  filter: brightness(85%);
}

img {
  margin: 0;
  object-fit: contain;
  object-position: center;
  max-height: 80%;
  width: 100%;
}

@media only screen and (max-width: 800px) {
  .page {
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: start;
  }
  .row {
    flex-direction: column;
    flex-wrap: nowrap;
    max-height: none;
  }

  .card {
    width: auto;
  }
}
/* FontAwesome for working BootSnippet :> */

#team .card {
  border: none;
  background: transparent;
}

.frontside {
  position: relative;
  -webkit-transform: rotateY(0deg);
  -ms-transform: rotateY(0deg);
  z-index: 2;
  margin-bottom: 30px;
}

.frontside .card,
.backside .card {
  min-height: 150px;
}

.frontside .card .card-title {
  color: var(--p) !important;
}

.frontside .card .card-body img {
  width: 90%;
  height: 120px;
  border-radius: 50%;
}

.text-title {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.text-title-button {
  margin-left: 15px;
  border-color: transparent;
  color: #409eff;
  background: transparent;
  padding-left: 0;
  padding-right: 0;
  outline: none;
  border: 0 none;
}

.text-title-button:focus {
  border: 0 none;
  outline: none;
}

.text-red {
  color: var(--red) !important;
}
.text-blue {
  color: var(--blue);
}
.text-green {
  color: var(--green);
}
.text-dark {
  color: var(--p) !important;
}

.navbar-light .navbar-nav .nav-link {
  color: var(--p);
}

.navbar-light .navbar-nav .active > .nav-link,
.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .nav-link.show,
.navbar-light .navbar-nav .show > .nav-link {
  color: var(--p) !important;
  border-bottom: 3px solid var(--p);
}

.navbar {
  background-color: transparent;
}
.btn-outline-dark {
  color: var(--p) !important;
  border-color: var(--p) !important;
}
.btn-outline-red {
  color: var(--red) !important;
  border-color: var(--red) !important;
}
.btn-outline-blue {
  color: var(--blue) !important;
  border-color: var(--blue) !important;
}
.btn-outline-green {
  color: var(--green) !important;
  border-color: var(--green) !important;
}

.btn-outline-red:hover {
  color: #eaeaea !important;
  border-color: var(--red) !important;
  background-color: var(--red);
}
.btn-outline-blue:hover {
  color: #eaeaea !important;
  border-color: var(--blue) !important;
  background-color: var(--blue);
}
.btn-outline-green:hover {
  color: #eaeaea !important;
  border-color: var(--green) !important;
  background-color: var(--green);
}
.btn-outline-blue:hover {
  color: #eaeaea !important;
  border-color: var(--blue) !important;
  background-color: var(--blue);
}

.list-group-item {
  background-color: transparent;
  color: var(--blue);
}
ul {
  overflow: auto;
}

.feedContainer {
  width: 100%;
  padding-left: 2%;
}
.list-group {
  max-height: 500px;
  width: 100%;
}

.list-group-item:hover {
  background-color: var(--blue);
  border-color: var(--blue);
  color: var(--b);
}

.list-group-item.active,
.list-group-item.active:hover,
.list-group-item.active:focus {
  z-index: 2;
  background-color: var(--blue);
  border-color: var(--b);
  color: var(--b);
}

.list-group-item-green {
  background-color: transparent;
  color: var(--green);
}
.list-group-item-green:hover {
  background-color: var(--green);
  border-color: var(--green);
  color: var(--b);
}
.list-group-item-green.active,
.list-group-item-green.active:hover,
.list-group-item-green.active:focus {
  z-index: 2;
  background-color: var(--green);
  border-color: var(--b);
  color: var(--b);
}

.list-group-item-red {
  background-color: transparent;
  color: var(--red);
}
.list-group-item-red:hover {
  background-color: var(--red);
  border-color: var(--red);
  color: var(--b);
}
.list-group-item-red.active,
.list-group-item-red.active:hover,
.list-group-item-red.active:focus {
  z-index: 2;
  background-color: var(--red);
  border-color: var(--b);
  color: var(--b);
}

.list-group-item-blue {
  background-color: transparent;
  color: var(--blue);
}
.list-group-item-blue:hover {
  background-color: var(--blue);
  border-color: var(--blue);
  color: var(--b);
}
.list-group-item-blue.active,
.list-group-item-blue.active:hover,
.list-group-item-blue.active:focus {
  z-index: 2;
  background-color: var(--blue);
  border-color: var(--b);
  color: var(--b);
}

.homeStat {
  border-left: 1px solid var(--red);
  border-right: 1px solid var(--red);
  border-radius: 2%;
}
.statsContent {
  margin-left: 2%;
}

.list-group-item-black {
  background-color: transparent;
  color: var(--p);
}
.list-group-item-black:hover {
  background-color: var(--p);
  border-color: var(--p);
  color: var(--b);
}
.list-group-item-black.active,
.list-group-item-black.active:hover,
.list-group-item-black.active:focus {
  z-index: 2;
  background-color: var(--p);
  border-color: var(--b);
  color: var(--b);
}

.aBlack {
  color: var(--p);
  font-size: 1.12rem;
}

.border-right-red {
  border-right: 2px solid var(--red);
}
.border-right-green {
  border-right: 2px solid var(--green);
}
.border-right-blue {
  border-right: 2px solid var(--blue);
}

.searchListSuggestion {
  position: relative;
  width: 15rem;
  background-color: #f1f1f1;
  border-style: solid;
  border-color: #d4d4d4;
  border-top: none;
  border-width: 0.1rem;
  margin-bottom: 0rem;
  text-align: flex-start;
  display: flex;
  cursor: pointer;
}

.searchListSuggestion:hover {
  background-color: #d4d4d4;
}

.search-list-page-group {
  max-height: 500px;
  overflow-x: hidden;
  overflow-y: scroll;
}
